import React from 'react';
import ToolBar from '../tool-bar';
class DetailPane extends React.Component {
    constructor() {
        super(...arguments);
        this.state = {};
        this.getBaseToolBarProps = () => {
            const { title, extra } = this.props;
            const style = {
                boxSizing: 'border-box',
                paddingLeft: '6px',
                borderLeft: '3px solid #1890ff',
                fontSize: '14px',
                fontWeight: 500,
                color: 'rgba(0, 0, 0, 0.85)',
            };
            return {
                wrapperStyle: {
                    margin: '10px 0',
                    height: 30,
                },
                left: <div style={style}>{title}</div>,
                right: extra,
            };
        };
    }
    render() {
        const { children, style } = this.props;
        const _style = Object.assign(Object.assign({ height: '100%' }, style), { position: 'relative', padding: '0 10px', overflow: 'hidden' });
        return (<div style={_style}>
        <ToolBar {...this.getBaseToolBarProps()}/>
        <div style={{
            position: 'absolute',
            left: 10,
            right: 10,
            bottom: 0,
            top: 40,
        }}>
          {children}
        </div>
      </div>);
    }
}
DetailPane.defaultProps = {
    title: '',
    extra: null,
};
export default DetailPane;
